nav {
    font-size: 30px;
    padding: 20px;
    background-color: rgb(85, 46, 46);
    color: white;
  }

  .title {
    float: left;
  }
  
  .header {
    float: left;
    margin-left: 25%;
  }
  .score {
    float: right;
  }
.jumbotron {
  background-image: url(/HaifaSeyam/clicky-game/static/media/img.37245ab7.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  min-height: 95vh;
  color: white;
  margin-bottom: 0;
}

.container {
  background-color: rgb(85, 46, 46, 0.6);
  max-width: 100%;
}

.container h1 {
  margin-top: 15%;
}
.card {
    display: inline-block;
    margin: 10px;
}

.cardImg {
    width: 150px;
    height: 150px;
    border: 3px solid white;
}

.cardImg:hover {
    cursor: pointer;
}
.containerDiv {
    background-color: rgb(85, 46, 46);
    padding: 3% 15%;
    padding-left: 17%;
}

a, a:hover {
    color: white;
    text-decoration: none !important;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

